import { Injectable } from '@angular/core';

import { Nation } from './nation';

@Injectable()
export class SelectedNationService {

  private selectedNation: Nation;

  constructor() { }

  setNation(nation: Nation) {
        this.selectedNation = nation;
    }//END setNation;

  getNation() {
        return this.selectedNation;
    }//END getNation
}//END export class



