import { Component, OnInit, Input, Output } from '@angular/core';

import { DataService } from '../../data.service';
import { CarouselFocusService } from './carousel-focus.service';


@Component({
  selector: 'fb-carousel',
  templateUrl: './carousel.component.html',
  styleUrls: ['./carousel.component.css']
})

export class CarouselComponent implements OnInit {

collection = [];
pictures;
@Input() country;

  constructor(private dataService:DataService, private carouselFocusService:CarouselFocusService) { }


  ngOnInit() {

    this.dataService.fetchPhotos(this.country.name)
.subscribe((data) => { this.pictures = (data)
  
  for(let i = 0; i < this.pictures.items.length; i++){

  this.collection.push(this.pictures.items[i].link);

  }//END for


})//END .subscribe

  }//END ngOnInit

pictureModal(picture){
   
  console.log(this.collection[picture]);
  
  /**sets selected picture in focus */
this.carouselFocusService.setPicture(this.collection[picture]);


  /**toggle selected modal */
  this.carouselFocusService.toggleFocus();

  console.log(this.carouselFocusService.isSelected)
}//END pictureModal()

}//END export class



