import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { Region } from '../region';
import { Nation } from '../nation';
import { DataService } from '../data.service';
import { SelectedNationService } from '../selected-nation.service';

@Component({
  selector: 'fb-nation-list',
  templateUrl: './nation-list.component.html',
  styleUrls: ['./nation-list.component.css']
})

export class NationListComponent implements OnInit {
  keyData;
  region;
  
  regions: Region[] = [];
  nations: Nation[] = [];

  constructor(private dataService: DataService, private router: Router, private selectedNation: SelectedNationService) { }

  ngOnInit() {
    /**Pulls in data to populate the menu with regions */
    this.dataService.fetchKey().subscribe((data) => {
      this.keyData = data;
      /**provide a less awkward handle that won't error for assignments in the for loop */
      let fromJSON = this.keyData;

      /**loop throught the regions and assign Region type to data */
      for (let i = 0; i < fromJSON.length; i++) {

        this.regions.push(fromJSON[i][0]['region'] = new Region(fromJSON[i][0]['region'], []));

      }//END for

      /** loop through the data and assign Nation type to each and push to the regions array */
      for (let i = 0; i < fromJSON.length; i++) {
        for (let j = 0; j < fromJSON[i].length; j++) {

          this.regions[i].nations.push(fromJSON[i][j]['place'] = new Nation(fromJSON[i][j]['place'], fromJSON[i][j]['region'], fromJSON[i][j]['code'], fromJSON[i][j]['apiname'], fromJSON[i][j]['flagcode']));

        }//END for j

      }//END for i
    
    })//END .subscribe

    
  }//END ngOnInit


  onRegion(region) {
    /**loops through the selected attribute and initializes to false */
    for (let i = 0; i < this.regions.length; i++) {

      this.regions[i].selected = false;
    }//END for
    /**sets current region's selected attribute to true */
    return this.regions[region].selected = true,

      /**reveals the list of nations for the specified region */
      this.nations = this.regions[region]['nations'];


  }//END onRegion()

  onNation(nation) {

    /**Assigns the selected nation to the SelectedNationService to inject into the main Nation view */
    this.selectedNation.setNation(this.nations[nation]);

    /**Routes user to main Nation view */
     this.router.navigate(['nation'])


  }//END onNation



}//END export class





