import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { SelectedNationService } from './selected-nation.service';

import { Observable, Subscription } from 'rxjs';
import 'rxjs/add/operator/map';
import * as Key from '../../config';

@Injectable()
export class DataService {

 
  fbRepoEndPoint = "https://raw.githubusercontent.com/opendatajson/factbook.json/master/";
  flagEndPoint = "https://raw.githubusercontent.com/hjnilsson/country-flags/master/png250px/";

  googleSearchKey = Key.config.google_search;

  constructor(private http: Http, selectedNation: SelectedNationService) { }

/** Service retrieves data to create Region and Nation objects */
fetchKey(){
  return this.http.get("./app/fbkeys.json")
  .map((response:Response) => response.json())
}//END fetchKey()

/** Service retrieves country specific data from API */
fetchData(apiname, code){
  return this.http.get(this.fbRepoEndPoint + apiname + "/" + code + ".json")
  .map( (response: Response) => response.json())
  
}//END fetchData()

/** Service retrievs flag */
fetchFlag(flagCode){

  return this.flagEndPoint + flagCode + ".png";

}//END fetchFlag()

fetchPhotos(countryName){
  return this.http.get("https://www.googleapis.com/customsearch/v1?parameters&key=" + this.googleSearchKey + "&cx=014576438316340577193:-zth3czqwts&searchType=image&q=" + countryName)
  .map((response:Response) => response.json())
}//END fetchPhotos()

}//END export class







