import { Component, OnInit } from '@angular/core';
import { NationListComponent } from '../nation-list/nation-list.component';


import { Nation } from '../nation';
import { SelectedNationService } from '../selected-nation.service';
import { DataService } from '../data.service';
import { LatlongService } from '../latlong.service';
import { MapComponent } from '../map.component';

import {PageScrollConfig} from 'ng2-page-scroll';

import { CarouselComponent } from './carousel/carousel.component';
import { GeographyDataComponent } from './geography-data/geography-data.component';
import { PeopleAndSocietyComponent } from './people-and-society/people-and-society.component';
import { GovernmentComponent } from './government/government.component';
import { EconomyComponent } from './economy/economy.component';
import { EnergyComponent } from './energy/energy.component';

@Component({
  selector: 'fb-nation-data',
  templateUrl: './nation-data.component.html',
  styleUrls: ['./nation-data.component.css']
})
export class NationDataComponent implements OnInit {

svg;
country;
nation;
latitude;
longitude;
flag;
public isCollapsed = true;



  constructor(private selectedNation:SelectedNationService, private dataService: DataService, private latlong: LatlongService) {
    /** adjust speed for scroll animation on Data Section dropdown menu */
    PageScrollConfig.defaultDuration = 300;
   }
  

  ngOnInit() {
    
    /**Pulls in data from selected nation service data */
    this.country = this.selectedNation.getNation();

    /**Redirects to main page on refresh or undefined this.country */
    if (!this.country){
      document.location.reload(true);
    }

    /**Retrieves the data for this nation from the API  */
    this.dataService.fetchData( this.country.apiname, this.country.code )
    .subscribe((data) => { this.nation = (data);

    console.log(this.nation);  

    /** Retrieve latitude data to pass to map component */
    this.latitude = this.latlong.getLat(this.nation);

    /** Retrieve longitude data to pass to map component */
    this.longitude = this.latlong.getLong(this.nation);

})//END .subscribe
  
  this.flag =  this.dataService.fetchFlag(this.country.flagcode.toLowerCase()) 
  {

      }//END fetchFlag()

  }//END ngOnInit


}//END export class 


