import { Injectable } from '@angular/core';

@Injectable()
export class CarouselFocusService {

  public isSelected: boolean = false;
  public focusedPicture: string;

  toggleFocus() {
        this.isSelected = !this.isSelected;
    }//END toggleFocus()

  setPicture(picture) {
        this.focusedPicture = picture;
    }//END setPicture()

  getPicture(){
    return this.focusedPicture;
  }//END getPicture()


  constructor() { }

}



