import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { WelcomeComponent } from './welcome/welcome.component';
import { NationDataComponent } from './nation-data/nation-data.component';
import { GeographyDataComponent } from './nation-data/geography-data/geography-data.component';

const APP_ROUTES: Routes = [ 
    {path:'', component: WelcomeComponent },
    {path:'nation', component: NationDataComponent },
    {path:'geography', component: GeographyDataComponent}
    ];

export const routing: ModuleWithProviders = RouterModule.forRoot(APP_ROUTES);


