import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AgmCoreModule } from 'angular2-google-maps/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import * as Key from '../../config';

import { routing } from './app.routing';
import { AppComponent } from './app.component';
import { WelcomeComponent } from './welcome/welcome.component';
import { DataService } from './data.service';
import { SelectedNationService } from './selected-nation.service';
import { NationListComponent } from './nation-list/nation-list.component';
import { NationDataComponent } from './nation-data/nation-data.component';
import { MapComponent } from './map.component';
import { LatlongService } from './latlong.service';
import { CarouselComponent } from './nation-data/carousel/carousel.component';
import { CarouselModalComponent } from './nation-data/carousel/carousel-modal/carousel-modal.component';
import { CarouselFocusService } from './nation-data/carousel/carousel-focus.service';
import { GeographyDataComponent } from './nation-data/geography-data/geography-data.component';
import { PeopleAndSocietyComponent } from './nation-data/people-and-society/people-and-society.component';
import { GovernmentComponent } from './nation-data/government/government.component';
import { EconomyComponent } from './nation-data/economy/economy.component';
import { EnergyComponent } from './nation-data/energy/energy.component';
import { TransportationComponent } from './nation-data/transportation/transportation.component';
import { CommunicationsComponent } from './nation-data/communications/communications.component';
import { MilitaryComponent } from './nation-data/military/military.component';
import { TransnationalIssuesComponent } from './nation-data/transnational-issues/transnational-issues.component';


@NgModule({
  declarations: [
    AppComponent,
    WelcomeComponent,
    NationListComponent,
    NationDataComponent,
    MapComponent,
    CarouselComponent,
    CarouselModalComponent,
    GeographyDataComponent,
    PeopleAndSocietyComponent,
    GovernmentComponent,
    EconomyComponent,
    EnergyComponent,
    TransportationComponent,
    CommunicationsComponent,
    MilitaryComponent,
    TransnationalIssuesComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    routing,
    AgmCoreModule.forRoot({
      apiKey: Key.config.maps_api
    }),
    NgbModule.forRoot(),
    Ng2PageScrollModule.forRoot()
  ],
  providers: [DataService, SelectedNationService, LatlongService, CarouselFocusService],
  bootstrap: [AppComponent]
})
export class AppModule { }



