import { Injectable, Output } from '@angular/core';
import { Http, Response } from '@angular/http';


import * as _ from 'lodash';
import { Observable, Subscription } from 'rxjs';
import 'rxjs/add/operator/map';


@Injectable()
export class LatlongService {


  coord;
  comma;
  commaSpace;
  latDirectionIndex;
  latDirection;
  longDirection;
  latIndex;
  lat;
  longStart;
  longEnd;
  longEndIndex;
  longitude;
  long;

  locatee;

  constructor() { }


  getLat(locatee) {

    /** If else... Certain coordinates data is located in an atypical place, this is for contingency */

    if (!locatee.Geography["Geographic coordinates"]) {
      this.coord = "50 50 N, 4 20 E";
    } else if (locatee.Geography['Geographic coordinates']["Saint Helena"]) {
      this.coord = locatee.Geography['Geographic coordinates']["Saint Helena"].text
      
    } else if (locatee.Geography["Geographic coordinates"]["metropolitan France"]) {
      this.coord = locatee.Geography["Geographic coordinates"]["metropolitan France"].text
    
    } else if (locatee.Geography["Geographic coordinates"]["Bassas da India (Iles Eparses)"]) {
      this.coord = locatee.Geography["Geographic coordinates"]["Bassas da India (Iles Eparses)"].text


    } else {
      /** raw string from API */
      this.coord = locatee.Geography['Geographic coordinates'].text;
    }
    /** location of the comma */
    this.comma = this.coord.indexOf(',');

    /** the space after the comma */
    this.commaSpace = Number(this.comma + 1);

    /** index of cardinal direction for latitude */
    this.latDirectionIndex = this.comma - 1;

    /** index of the end of the latitude coordinates */
    this.latIndex = this.latDirectionIndex - 1;

    /** cardinal direction for longitude */
    this.longDirection = this.coord.substr(this.coord.length - 1);

    /** cardinal direction for latitude */
    this.latDirection = this.coord.charAt(this.latDirectionIndex);

    /** start of longitude coordinate */
    this.longStart = this.commaSpace + 1;

    /** index of last number in longitude */
    this.longEndIndex = Number(this.coord.indexOf(this.longDirection));

    /** character at last index of longitude */
    this.longEnd = this.coord.charAt(this.longEndIndex);

    /** full longitude formatted*/
    this.longitude = this.coord.substr(this.longStart).replace(" ", ".");

    /** longitude coordinates formatted with a decimal */
    this.long = _.truncate(this.longitude, { length: 5, 'omission': '' });

    /** latitude coordinates formatted with a decimal */
    this.lat = this.coord.substr(0, this.latIndex).replace(" ", ".")

    /**change type of output to number */
    Number(this.lat);

    if (this.latDirection == "S") {
      this.lat *= -1;
    }//END if

    return Number(this.lat);

  }//END getLat();

  getLong(locatee) {

    /** If else... Certain coordinates data is located in atypical place, this is for contingency */
    
    if (!locatee.Geography["Geographic coordinates"]) {
      this.coord = "50 50 N, 4 20 E";
    } else if (locatee.Geography['Geographic coordinates']["Saint Helena"]) {
      this.coord = locatee.Geography['Geographic coordinates']["Saint Helena"].text

    } else if (locatee.Geography["Geographic coordinates"]["metropolitan France"]) {
      this.coord = locatee.Geography["Geographic coordinates"]["metropolitan France"].text
    
    } else if (locatee.Geography["Geographic coordinates"]["Bassas da India (Iles Eparses)"]) {
      this.coord = locatee.Geography["Geographic coordinates"]["Bassas da India (Iles Eparses)"].text

    } else {
      /** raw string from API */
      this.coord = locatee.Geography['Geographic coordinates'].text;
    }
    /** location of the comma */
    this.comma = this.coord.indexOf(',');

    /** the space after the comma */
    this.commaSpace = Number(this.comma + 1);

    /** index of cardinal direction for latitude */
    this.latDirectionIndex = this.comma - 1;

    /** index of the end of the latitude coordinates */
    this.latIndex = this.latDirectionIndex - 1;

    /** cardinal direction for longitude */
    this.longDirection = this.coord.substr(this.coord.length - 1);

    /** cardinal direction for latitude */
    this.latDirection = this.coord.charAt(this.latDirectionIndex);

    /** start of longitude coordinate */
    this.longStart = this.commaSpace + 1;

    /** index of last number in longitude */
    this.longEndIndex = Number(this.coord.indexOf(this.longDirection));

    /** character at last index of longitude */
    this.longEnd = this.coord.charAt(this.longEndIndex);

    /** full longitude formatted*/
    this.longitude = this.coord.substr(this.longStart).replace(" ", ".");

    /** longitude coordinates formatted with a decimal */
    this.long = _.truncate(this.longitude, { length: 5, 'omission': '' });

    /** latitude coordinates formatted with a decimal */
    this.lat = this.coord.substr(0, this.latIndex).replace(" ", ".")

    /** Change output type to a number */
    Number(this.long);

    /** Converts cardinal directions to negative value if needed */
    if (this.longDirection == "W") {
      this.long *= -1;
    }//END if
    return Number(this.long);

  }//END getLong

}//END export class


